/*----------------------------------------------
    # Nav bar
----------------------------------------------*/
.navbar-top {
  background: #080c24;
  position: relative;
  margin-bottom: 68px;
  padding: 16.5px 0;
  &:after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 66.8%;
    background: url("~/public/assets/img/shape/topbar-bg.png");
    background-position: 0 0px;
    background-size: 165% 100%;
    height: 100%;
    z-index: -1;
  }
  .nav-phone-wrap {
    background: url("~/public/assets/img/shape/nav-contact-bg.png");
    background-position: -38px 0px;
    background-size: 112% 100%;
    background-repeat: no-repeat;
    display: inline-block;
    position: absolute;
    top: 90px;
    color: #fff;
    padding: 12.5px 135px 9.5px 115px;
    left: -70px;
    font-family: var(--heading-font);
    font-size: 14px;
    i,
    svg {
      margin-right: 5px;
    }
  }
  .logo {
    position: absolute;
    left: -70px;
    top: 0;
    width: 530px;
    .main-logo {
      display: inline-block;
    }
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    &.topbar-left {
      margin-left: 369px;
      li {
        + li {
          margin-left: 11px;
        }
        i,
        svg {
          margin-right: 7px;
          color: var(--main-color);
        }
      }
    }
    &.topbar-right {
      li {
        + li {
          margin-left: 17px;
        }
      }
    }
    li {
      display: inline-block;
      color: #fff;
      font-size: 14px;
      a {
        color: #fff;
        &:hover {
          color: var(--main-color);
        }
      }
    }
  }
}
.navbar-area {
  z-index: 99;
  position: absolute;
  width: 100%;
  .nav-container {
    background-color: transparent;
    padding: 17px 0px;
    transition: all 0.4s;
    .btn-transparent {
      font-size: 13px;
      font-weight: 700;
    }
    .navbar-collapse {
      .navbar-nav {
        display: block;
        width: 100%;
        text-align: left;
        li {
          a {
            color: var(--heading-color);
            font-weight: 500;
            font-size: 16px;
          }
          &:hover a {
            color: #fb493d;
          }
          + li {
            margin-left: 15px;
          }
          display: inline-block;
          font-weight: 500;
          line-height: 50px;
          text-transform: capitalize;
          &.menu-item-has-children {
            position: relative;
            z-index: 0;
            padding-right: 14px;
            &:after {
              content: "";
              position: absolute;
              right: -1px;
              top: 26px;
              height: 2px;
              width: 10px;
              background: var(--heading-color);
              transform: translateY(-50%);
              transition: all 0.3s ease-in;
              z-index: -1;
            }
            &:before {
              content: "";
              position: absolute;
              right: 3px;
              top: 50%;
              height: 10px;
              width: 2px;
              background: var(--heading-color);
              transform: translateY(-50%);
              transition: all 0.3s ease-in;
              margin-top: 1px;
              z-index: -1;
            }
            &:hover {
              @include transition(all, 0.4s, ease);
              &:before {
                opacity: 0;
              }
              &:after {
                background: var(--main-color);
              }
            }
            &:hover > .sub-menu {
              visibility: visible;
              opacity: 1;
            }
            .sub-menu {
              position: absolute;
              text-align: left;
              min-width: 210px;
              margin: 0;
              padding: 0;
              list-style: none;
              left: 0;
              top: 100%;
              box-shadow: 0 0 15px 0 rgba(#000, 0.05);
              background-color: $color-white;
              z-index: 9;
              overflow: hidden;
              visibility: hidden;
              opacity: 0;
              @include transition(all, 0.4s, ease);
              border-radius: 0;
              &.border-bt0 {
                border-bottom: 0px !important;
              }
              li {
                display: block;
                margin-left: 0;
                line-height: 22px;
                font-size: 15px;
                @include transition(all, 0.4s, ease);
                border-bottom: 1px solid #f5f5f5;
                a {
                  display: block;
                  padding: 10px 20px;
                  white-space: nowrap;
                  transition: all 0.3s;
                  color: #050a30;
                  font-size: 14px;
                  font-weight: 500;
                  @include transition(all, 0.4s, ease);
                  position: relative;
                }
                &:hover {
                  background: #fb493d;
                  a {
                    color: #fff;
                    &:before {
                      visibility: visible;
                      opacity: 1;
                    }
                  }
                }
                &:last-child {
                  border-bottom: 0;
                }
              }
              .menu-item-has-children {
                position: relative;
                z-index: 0;
                padding-right: 0px;
                > .sub-menu {
                  left: 100%;
                  top: 20px;
                  .sub-menu {
                    .sub-menu {
                      left: auto;
                      right: 100%;
                    }
                  }
                }
                &:hover > .sub-menu {
                  visibility: visible;
                  opacity: 1;
                  li {
                    &:hover {
                      &:before {
                        color: $color-white;
                      }
                    }
                  }
                }
              }
            }
          }
          &:last-child {
            margin-right: 0;
          }
        }
        > li {
          margin-right: 10px;
        }
      }
    }
  }
}
